body {
  margin: 0;
  padding: 0;
  color: rgb(50, 50, 50);
  font-family: Arial, Helvetica, sans-serif;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1 {
  margin: 0;
  text-transform: uppercase;
  text-align: center;
}

.container {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1300px;
  padding: 20px;
}
